import React from 'react'
import {Link} from 'react-router-dom';
import { TokenUtil } from '../Modules/util/TokenUtil';
import * as userReducer from "../../src/Modules/products/Redux/users/user.reducer";
import { useSelector } from 'react-redux';
import { Appdispatch, RootState, useAppDispatch } from '../Modules/products/Redux/Store';
import { logOutUserAction } from '../../src/Modules/products/Redux/users/user.reducer';

interface IProps{
    heading:string;
}
const Navbar:React.FC <IProps>= (props) => {
           const disatch:Appdispatch = useAppDispatch();

  //get user from redux
  const userState:userReducer.InitialState = useSelector((state:RootState)=>{
    return state[userReducer.userFeatureKey];
   })
   const clickLogOff=():void=>{
    disatch({
      type:`${logOutUserAction}`,
      payload:{}
    })

   }

   const {user:userReduxState} =userState;
  return (
    <div>
      <nav className='navbar bg-dark navbar-expand-sm navbar-dark'>
        <div className='container'>
            <Link to="/" className='navbar-brand'><i className='bi bi-cart'></i>{props.heading.split(' ')[0]}
            <span className='text-warning'> {props.heading.split(' ')[1]}</span>
            </Link>
            <div className='navbar-collapse collapse'>
                        {
                        TokenUtil.IsLoggedIn() && <ul className='navbar-nav me-auto'>
                        
                          <li className='nav-item'>
                            <Link to="/pages/Display" className='nav-link'>Display</Link>
                          </li>
                          {userReduxState.isAdmin && <li className='nav-item '>
                            <Link to="/pages/Admin" className='nav-link'>Admin</Link>
                          </li>}
                          </ul>
                          }
                          <ul className='navbar-nav ms-auto'>
                            {
                              TokenUtil.IsLoggedIn() ? <>
                             
                            {
                              userReduxState && Object.keys(userReduxState).length>0 &&
                              <li className='nav-item '>
                              <Link to="/" className='nav-link'>
                              <img src={userReduxState.imageurl} width={25} height={25} alt="/" className='rounded-circle'/> {userReduxState.username}
                              </Link>
                            </li>

                            }

                              <li className='nav-item '>
                              <Link to="/" className='nav-link'><i className='bi bi-power' onClick={clickLogOff}></i></Link>
                            </li>

                              </>
                              :<li className='nav-item '>
                               <Link to="/users/login" className='nav-link'>Login</Link>
                             </li>
                            }
                          
                         
                         
                        </ul>
            </div>
        </div>
      </nav>
    </div>
  )
}

export default Navbar
