import React, { useEffect, useState } from 'react'
import Navbar from '../../../layouts/Navbar'
import LayoutHeading from '../../../layouts/layoutheading'
import { IProductview } from '../models/IProductview';
import { useNavigate } from 'react-router-dom'
import { RootState, useAppDispatch } from '../Redux/Store'
import * as productActions from '../Redux/products/product.actions'
import * as productReducer from '../Redux/products/product.reducer'
import { useSelector } from 'react-redux'

const Addproduct = () => {
    const navigate=useNavigate();
    const disatch= useAppDispatch();
    //get the state from redux
    const productReduxState:productReducer.InitialState= useSelector((state:RootState)=>{
        return state[productReducer.productFeatureKey]
    })
    const{loading,errormessage,groups}=productReduxState;

    const[product,setProduct]=useState<IProductview>({
        name:"",
        imageurl:"",
        qty: 0,
        price: 0,
        groupId:""
    }   
    )   
   

    useEffect(()=>{
      /**  ProductService.getAllgroups().then((response)=>{
        setGroups(response.data)
       }).catch((error)=>{
        console.error(error);
       })*/
      disatch(productActions.getAllGroupsAction())

    },[])
    const changeinput=(event:React.ChangeEvent<HTMLInputElement|HTMLSelectElement|any>)=>{
        const{name,value}=event.target
        setProduct({

            ...product,
            [name]:name === 'qty' || name === 'price'? Number(value): value
        })

    }
    
const handleSubmit=(event:React.FormEvent<HTMLFormElement>)=>{
event.preventDefault();
/*ProductService.createProduct(product).then((response)=>{
    if(response && response.data){
        navigate("/pages/admin");
    }
}).catch((error)=>{
    console.error(error)
})*/
disatch(productActions.createAProductsAction({product:product})).then((response:any)=>{
    if(response && !response.err){
        navigate("/pages/admin");
    }
})
    }
  return (
    <div>
       <Navbar heading={'Product Manager'}/> 
       <LayoutHeading heading={'Addproduct'}/>
       <pre>{JSON.stringify(product)}</pre>
                    <div className='container mt-3'>
                    <div className='row'>
                          <div className='col-sm-4'>
                                    <form onSubmit={e=>handleSubmit(e)}>
                                        <div className="mb-2">
                                            <input
                                            value={product.name} 
                                            name={"name"}
                                            onChange={e=>changeinput(e)}
                                            className="form-control"required type ="text" placeholder="name"/>
                                        </div>
                                        <div className="mb-2">
                                            <input 
                                            value={product.imageurl} 
                                            name={"imageurl"}
                                            onChange={e=>changeinput(e)}className="form-control"required type ="imageurl"placeholder="imageurl"/>
                                        </div>
                                        <div className="mb-2">
                                            <input 
                                            value={product.qty} 
                                            name={"qty"}
                                            onChange={e=>changeinput(e)}className="form-control"required type ="qty"placeholder="qty"/>
                                        </div>
                                        <div className="mb-2">
                                            <input
                                            value={product.price} 
                                            name={"price"}
                                            onChange={e=>changeinput(e)}
                                             className="form-control"required type ="price"placeholder="price"/>
                                        </div>
                                        

                                        <div className="mb-2" >
                                            <select 
                                            value={product.groupId} 
                                            name={"groupId"}
                                            onChange={e=>changeinput(e)}className="form-control" required   >
                                            <option value="">select A group</option>
                                             {
                                                groups && groups.length>0 && groups.map((group,index)=>{
                                                    return(
                                                        <option  value={group._id}>{group.name}</option>
                                                    )
                                                })
                                             }
                                            </select>
                                        </div>
                                        <div className="mb-2">
                                                <input className="btn btn-primary me-2" type="submit" />
                                                <a href="/pages/Admin" className="btn btn-dark" >cancel</a>
                                            
                                        </div>
                                    </form>
                          </div>
                                  <div className="col-sm-2">
                                      <img className=" img-fluid "  src={product.imageurl} alt="/"/>
                                  </div>
                  </div>
                  </div>
          </div>
      
  )
}

export default Addproduct
