import React from 'react'
import Navbar from '../../../layouts/Navbar'
import LayoutHeading from '../../../layouts/layoutheading'
import { Link, useNavigate } from 'react-router-dom'
import { useEffect } from 'react'
import Errormessage from '../../../layouts/Errormessage'
import Spinner from '../../../layouts/Spinner'
import { useParams } from 'react-router-dom'
import { RootState, useAppDispatch } from '../Redux/Store'
import * as  productActions from '../Redux/products/product.actions'
import * as productReducer from '../Redux/products/product.reducer'
import { useSelector } from 'react-redux'

const Adminproduct = () => {
  
  const dispatch = useAppDispatch();
  //get state from redux

  const productReduxState:productReducer.InitialState=useSelector((state:RootState)=>{
    return state[productReducer.productFeatureKey]
  })
  const {products,product,errormessage,loading}=productReduxState;
  useEffect(()=>{
   
getProductdatafromserver();
  },[]);

  const getProductdatafromserver=()=>{
    dispatch(productActions.getAllProductsAction())
  }


 
const Deleteproduct=(productId:string|undefined)=>{
if(productId){
 /* ProductService.DeleteAProduct(productId).then((response)=>{
     if(response && response.data){
      getProductdatafromserver();
      navigate("/pages/admin");
     }
  }).catch((error)=>{
    setState({...state,loading:false,errorMessage:errorMessage})
  })
}*/
  dispatch(productActions.deleteProductsAction({productId:productId})).then((response:any)=>{
    if(response && !response.err){
      getProductdatafromserver();
    }
  })
}

}
  

  return (
    <div>
      {loading && <Spinner/>}
      <Navbar heading={"productManager"}/>
      <LayoutHeading heading={"Admin products"}/>
      <div className='container mt-3'>
        <Link to="/pages/addproduct" className='btn btn-success'>New</Link>
      <table className='table table-hover text-center mt-2'>
        <thead className='table-dark'>
            <tr>
                    <th>sno</th>
                    <th>image</th>
                    <th>name</th>
                    <th>qty</th>
                    <th>price</th>
                    <th>total</th>
                    <th>availability</th>
                    <th>action</th>
                
            </tr>
            </thead>
            <tbody>
               {
                !loading && products && products.length>0 &&
                products.map((product,index)=>{
                  return(
                    <tr key={(product._id)}>
                       
                    <td>{index+1}</td>
                    <td><img src={product.imageurl} className='img-fluid' width="50" height="50"alt="/"></img></td>
                    <td>{product.name}</td>
                    <td>{product.qty}</td>
                    <td>{product.price}</td>
                    <td>{product.qty*product.price}</td>  
                    <td>{
                    (product.qty >=10 && product.qty <=20)
                     ?'Runningoutofstock'
                     :product.qty >0
                     ? 'available'
                     :'outof stock'
                }
                    </td>
                    <td><button className='btn btn-success'onClick={()=>Deleteproduct(product._id)}><i className='bi bi-trash-fill'></i></button>
                    <Link  to={`/pages/edit/${product._id}`}className='btn btn-primary ms-2'><i className='bi bi-pencil-square '></i></Link></td>
                </tr>
                    
                  )
                })
               }
                
               
            </tbody>
        
      </table>
      <div className='row'>
        <div className='text-center'>
      {
        !loading && products.length === 0 && <Errormessage message={'----No products found----'}/>
      }
      </div>
    </div>
      </div>
    </div>
  )
}

export default Adminproduct
