import React, { useState ,useEffect} from 'react'
import Navbar from '../../../layouts/Navbar'
import LayoutHeading from '../../../layouts/layoutheading'
import Displayproductcart from '../../products/components/Displayproductcart';
import Errormessage from '../../../layouts/Errormessage'
import Spinner from '../../../layouts/Spinner';
import * as productReducer from '../Redux/products/product.reducer';
import * as productActions from '../Redux/products/product.actions'
import { RootState, useAppDispatch } from '../Redux/Store'
import { useSelector } from 'react-redux'

const Displayproduct:React.FC = () => {
  const dispatch=useAppDispatch();

  //get product state from redux

  const productReduxState:productReducer.InitialState=useSelector((state:RootState)=>{
      return state[productReducer.productFeatureKey]
  })

  useEffect(()=>{
    dispatch(productActions.getAllProductsAction())
    
  },[dispatch]);
  const {products,errormessage,loading}=productReduxState;
  return (
    <div>
      {loading && <Spinner/>}
    <Navbar heading={'Product Manager'}/> 
    <LayoutHeading heading={'Displayproducts'}/>
    {!loading && errormessage && (<Errormessage message={errormessage}/>)} {/*if it is null know need to write the errorMessage > 0*/}
    <div className='container mt-3'>
        <div className='row'>
           
           {
              !loading  && products && products.length >0 &&
              products.map((product,index)=>{
                return(
                  <div key={product._id} className='col-sm-3 mb-2'>
                  <Displayproductcart product={product} /> 
                  </div>
                )
              })
            }
        </div>
    
    <div className='row'>
      {
        !loading && products && products.length === 0 && <Errormessage message={'No products found'}/>
      }
    </div>
</div>
    </div>
  )
}

export default Displayproduct
