import React, { useEffect } from 'react'
import Navbar from './layouts/Navbar'
import { BrowserRouter,Routes,Route} from 'react-router-dom'
import HomePage from '../src/Modules/ui/HomePage';
import { ToastContainer } from 'react-toastify';
import PageNotFound from '../src/Modules/ui/PageNotFound';
import Addproduct from '../src/Modules/products/pages/Addproduct';
import Viewproduct from '../src/Modules/products/pages/Viewproduct';
import Editproduct from '../src/Modules/products/pages/Editproduct';
import Adminproduct from '../src/Modules/products/pages/Adminproduct';
import Displayproduct from '../src/Modules/products/pages/Displayproduct';
import Userlogin from './Modules/users/pages/Userlogin';
import UserRegister from './Modules/users/pages/UserRegister';
import ReactToastfyConfiguration from './components/ReactToastfyConfiguration';
import { TokenUtil } from './Modules/util/TokenUtil';
import { Appdispatch, useAppDispatch } from './Modules/products/Redux/Store';
 import  * as useractions from "../src/Modules/products/Redux/users/user.actions";
import PrivateRoute from './Routes/PrivateRoute';
import AdminRoute from './Routes/AdminRoute';



const App = () => {
  const dispatch:Appdispatch = useAppDispatch();

  useEffect(()=>{
    if(TokenUtil.IsLoggedIn()){
      dispatch(useractions.getUserInfoAction())
    }
  },[dispatch])
  return (
    <div>
    

      <BrowserRouter>
      
      {/**React Toastfy Configuration */}
      <ReactToastfyConfiguration/>
      <Routes>
        <Route path={"/"} element={<HomePage/>}/>
        <Route path={"/pages/Display"} element={
          <PrivateRoute>
          <Displayproduct/>
          </PrivateRoute>}/>
        <Route path={"/pages/Admin"} element={
          <AdminRoute>
            <Adminproduct/>
            </AdminRoute>}/>
        <Route path={"/pages/addproduct"} element={
          <AdminRoute>
          <Addproduct/>
          </AdminRoute>}/>
        <Route path={"/pages/view/:productId"} element={
          <PrivateRoute>
            <Viewproduct/>
            </PrivateRoute>}/>
        <Route path={"/pages/edit/:productId"} element={
          <AdminRoute>
            <Editproduct/>
            </AdminRoute>}/> 
        <Route path={"*"} element={<PageNotFound/>}/>
        <Route path={"/users/login"} element={<Userlogin/>}/>
        <Route path={"/users/Register"} element={<UserRegister/>}/>

      </Routes>
      </BrowserRouter>
    </div>
  )
}

export default App;
