import React from 'react'
import { useState } from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import { IGroupview } from '../models/IGroupview';
import { IProductview } from '../models/IProductview';
import { useEffect } from 'react';
import { ProductService } from '../services/ProductService';
import Navbar from '../../../layouts/Navbar';
import LayoutHeading from '../../../layouts/layoutheading';
import Spinner from '../../../layouts/Spinner';
import Errormessage from '../../../layouts/Errormessage';
import { RootState, useAppDispatch } from '../Redux/Store';
import * as  productActions from '../Redux/products/product.actions'
import * as productReducer from '../Redux/products/product.reducer'
import { useSelector } from 'react-redux';


const Editproduct = () => {
  const navigate=useNavigate();
  const {productId}=useParams();
  const dispatch = useAppDispatch();

  //get state from redux

  const productReduxState:productReducer.InitialState=useSelector((state:RootState)=>{
    return state[productReducer.productFeatureKey]
  })  
  const {product:productRedux,errormessage,loading,groups}=productReduxState;

  const[productlocal,setProductlocal]=useState<IProductview>({
    name:"",
    imageurl:"",
    qty: 0,
    price: 0,
    groupId:""
}   
)

  useEffect(()=>{
    if(productId){
      getdataFromserver(productId);
    }
  },[productId])//when the change happens
  useEffect(()=>{
    getGroupdataFromserver();
  },[])

  useEffect(()=>{
    if(productRedux && Object.keys(productRedux).length>0){
      setProductlocal({
        ...productlocal,
              name:productRedux.name,
              imageurl:productRedux.imageurl,
              qty:productRedux.qty,
              price: productRedux.price,
              groupId:productRedux.groupId
      })
    }
  },[productRedux])

 
  const getdataFromserver=(productId:string)=>{
  dispatch(productActions.getAProductAction({productId:productId}))

  }
  const getGroupdataFromserver=()=>{
   dispatch(productActions.getAllGroupsAction())
  }
  const changeinput=(event:React.ChangeEvent<HTMLInputElement|HTMLSelectElement|any>)=>{
    const{name,value}=event.target
    setProductlocal({
        ...productlocal,
       [name]:name === 'qty' || name === 'price'? Number(value): value
  })
  }
  const handleSubmit=(event:React.FormEvent<HTMLFormElement>)=>{
    event.preventDefault();
    
     /* ProductService.updateAProduct(productId,product).then((response)=>{
        if(response && response.data){
            navigate("/pages/admin");
        }
    }).catch((error)=>{
        console.error(error)
    })
        }*/
       dispatch(productActions.updateProductsAction({productId:productId,product:productlocal})).then((response:any)=>{
        if(response && !response.err){
          navigate("/pages/admin");
        }
       })
}
 

  return (
    <div>

{loading && <Spinner/>}
        <Navbar heading={"Contact manager"}/>
        <LayoutHeading heading={"Edit product"}/>
  {!loading && errormessage && <Errormessage message={errormessage}/>} {/*if it is null know need to write the errorMessage > 0*/}
    
                    <div className='container mt-3'>
                    <div className='row'>
                          <div className='col-sm-4'>
                                    <form onSubmit={e=>handleSubmit(e)}>
                                        <div className="mb-2">
                                            <input
                                            value={productlocal.name} 
                                            name={"name"}
                                            onChange={e=>changeinput(e)}
                                            className="form-control"required type ="text" placeholder="name"/>
                                        </div>
                                        <div className="mb-2">
                                            <input 
                                            value={productlocal.imageurl} 
                                            name={"imageurl"}
                                            onChange={e=>changeinput(e)}className="form-control"required type ="imageurl"placeholder="imageurl"/>
                                        </div>
                                        <div className="mb-2">
                                            <input 
                                            value={productlocal.qty} 
                                            name={"qty"}
                                            onChange={e=>changeinput(e)}className="form-control"required type ="number"placeholder="qty"/>
                                        </div>
                                        <div className="mb-2">
                                            <input
                                            value={productlocal.price} 
                                            name={"price"}
                                            onChange={e=>changeinput(e)}
                                             className="form-control"required type ="price"placeholder="price"/>
                                        </div>
                                        

                                        <div className="mb-2" >
                                            <select 
                                            value={productlocal.groupId} 
                                            name={"groupId"}
                                            onChange={e=>changeinput(e)}className="form-control" required   >
                                            <option value="">select A group</option>
                                             {
                                                groups && groups.length>0 && groups.map((group,index)=>{
                                                    return(
                                                        <option value={group._id}>{group.name}</option>
                                                    )
                                                })
                                             }
                                            </select>
                                        </div>
                                        <div className="mb-2">
                                                <input className="btn btn-primary me-2" type="submit" />
                                                <a href="/pages/Admin" className="btn btn-dark" >cancel</a>
                                            
                                        </div>
                                    </form>
                          </div>
                                  <div className="col-sm-2">
                                      <img className=" img-fluid "  src={productlocal.imageurl} alt="/"/>
                                  </div>
                  </div>
                  </div>
          </div>
      
  )
}

      
  
export default Editproduct
