import { createAsyncThunk} from "@reduxjs/toolkit";
import { IUserview } from "../../../users/models/IUserview";
import { IUser } from "../../../../../server/database/Models/IUser";
import { UserService } from "../../../users/services/UserService";
import { AuthUtil } from "../../../util/AuthUtil";

/**Register User */

export const registerUserAction:any = createAsyncThunk('users/registerUserAction',
    async(payload:{user:IUserview},{rejectWithValue}):Promise<{msg:string,user:IUser}|any>=>{
      try{
        const {user}= payload;
        let response= await UserService.RegisterUser(user);
        return response.data;
      }catch(err:any){
           if(!err.response){
            throw err
           }
           return rejectWithValue(err.response.data);
      }
    })

    /** Login User */

export const loginUserAction:any = createAsyncThunk('users/LoginUserAction',
    async(payload:{user:IUserview},{rejectWithValue}):Promise<{msg:string,token:string,user:IUser}|any>=>{
      try{
        const {user}= payload;
        let response= await UserService.LoginUser(user);
        return response.data;
      }catch(err:any){
           if(!err.response){
            throw err
           }
           return rejectWithValue(err.response.data);
      }
    })
    /**
     * Get user Info
     * Private
     */
    export const getUserInfoAction:any = createAsyncThunk('users/getUserInfoAction',
        async(payload:{},{rejectWithValue}):Promise<{user:IUser}|any>=>{
          try{
            
           if(AuthUtil.isSetTokenToRequestHeader()){
            let response= await UserService.GetUserInfo();
            return response.data;
           }
          }catch(err:any){
               if(!err.response){
                throw err
               }
               return rejectWithValue(err.response.data);
          }
        })