import React from 'react'
import { Link } from 'react-router-dom'
import { IProductview } from '../models/IProductview';
interface IProps{
    product:IProductview;
}

const Displayproductcart:React.FC<IProps> = (props) => {
    const {product}=props;
  return (
    <div>
       
            <div className='card'>
                <div className='card-header'>
                   <Link to={`/pages/view/${product._id}`}> <img src={product.imageurl} className="img-fluid" alt="/"></img></Link>
                </div>
                <div className='card-body'>
                    <ul className='list-group'>
                        <li className='list-group-item'>name:{product.name}</li>
                        <li className='list-group-item'><select className='form-select'><option value={product.qty}>qty:{product.qty}kg</option></select></li>
                        <li className='list-group-item'>price:{product.price}</li>
                        
                    </ul>
                </div>
            </div>
        </div>

  )
}

export default Displayproductcart
