import React from 'react'
import { ToastContainer } from 'react-toastify';

const ReactToastfyConfiguration:React.FC = () => {
  return (
    <div>
      <ToastContainer
position="top-right"
autoClose={5000}
hideProgressBar={false}
newestOnTop={false}
closeOnClick
rtl={false}
pauseOnFocusLoss
draggable
pauseOnHover
theme="light"
/>

    </div>
  )
}

export default ReactToastfyConfiguration

