import { createAsyncThunk} from "@reduxjs/toolkit";
import { IProductview } from "../../models/IProductview"; 
import { IGroupview } from "../../models/IGroupview";
import { ProductService } from "../../services/ProductService";
import { AuthUtil } from "../../../util/AuthUtil";
/**
 * PRIVATE
 */
export const getAllProductsAction:any = createAsyncThunk('products/getAllProductsAction',
    async(payload:{},{rejectWithValue}):Promise<IProductview[]|any>=>{
      try{
        if(AuthUtil.isSetTokenToRequestHeader()){
        let response= await ProductService.getAllProducts();
        return response.data;
        }
      }catch(err:any){
           if(!err.response){
            throw err
           }
           return rejectWithValue(err.response.data);
      }
    })
export const getAProductAction:any = createAsyncThunk('products/getAProductAction',
    async(payload:{productId:string},{rejectWithValue}):Promise<IProductview|any>=>{
        
        try{
            if(AuthUtil.isSetTokenToRequestHeader()){
                const {productId}= payload;
        let response= await ProductService.getAProduct(productId);
        return response.data;
            }
        }catch(err:any){
            if(!err.response){
            throw err
            }
            return rejectWithValue(err.response.data);
        }
    })
