import React, { useState } from 'react'
import Navbar from '../../../layouts/Navbar'
import LayoutHeading from '../../../layouts/layoutheading'
import { Link, useNavigate } from 'react-router-dom'
import { Appdispatch, RootState, useAppDispatch } from '../../products/Redux/Store'
import * as useractions from "../../products/Redux/users/user.actions";
import * as userReducer from "../../products/Redux/users/user.reducer";
import { useSelector } from 'react-redux'
import { IUserview } from '../models/IUserview'
import Spinner from '../../../layouts/Spinner'

const Userlogin = () => {
  const dispatch:Appdispatch = useAppDispatch();
  const navigate= useNavigate();

  //get user from redux
   const userState:userReducer.InitialState = useSelector((state:RootState)=>{
    return state[userReducer.userFeatureKey];
   })
   const {loading}= userState;
  const [user,setUser]  =useState<IUserview>({
         username:"",
         email:"",
         password:""

  })
  const updateInput = (event:React.ChangeEvent<HTMLInputElement>)=>{
       setUser({
            ...user,
            [event.target.name]:event.target.value
       })
  }
  const handlesubmit=(event:React.FormEvent <HTMLFormElement>)=>{
    event.preventDefault();
    dispatch(useractions.loginUserAction({user:user})).then((response:any)=>{
      if(response && !response.error){
        navigate('/')
      }
    })
 //TODO  dispatch action
  }
  return (
    <div>
      {loading && <Spinner/>}
      <Navbar heading={"ProductManager"}/>
      <LayoutHeading heading={"LoginUser"}/>
      <div className='container mt-3'>
        <div className='row'>
          <div className='col-sm-4'>
            <form onSubmit={e=>handlesubmit(e)}>
            <div className='mb-2'>
              <input type="email"
               required={true}
               name={'email'}
               value={user.email}
               onChange={e => updateInput(e)}className='form-control' placeholder={'email'}/>

            </div>
            <div className='mb-2'>
              <input type="password"
               required={true}
               name={'password'}
               value={user.password}
               onChange={e => updateInput(e)}className='form-control' placeholder={'password'}/>

            </div>
            <div className='mb-2'>
              <input type="submit" className='btn btn-success me-2' value={"login"}/>
              <Link to={"/"} className="btn btn-danger">Cancel</Link>
            </div>
            </form>
            <small>don't have account ?  <Link to={"/users/Register"} className='text-decoration-none text-success'>Register</Link></small>

          </div>
        </div>
      </div>
    </div>
  )
}

export default Userlogin
