import React from 'react'
import { Link } from 'react-router-dom'
import { IProductview } from '../models/IProductview'
import { IGroupview } from '../models/IGroupview'
interface IProps{
    product:IProductview;
    group:IGroupview;
}
const Viewproductcard:React.FC<IProps> = (Props) => {
    const{product,group}=Props;
  return (
    <div>
        <div className='container mt-3'>
        <div className='row'>
      <div className='col-sm-2'>
                   <Link to="/pages/viewproduct"> <img src={product.imageurl} className="img-fluid rounded-circle" alt="/"></img></Link>
                </div>
                <div className='col-sm-6'>
               <div className='card'>
                
                <div className='card-body'>
                    <ul className='list-group'>
                        <li className='list-group-item'>name:{product.name}</li>
                        <li className='list-group-item'>qty:{product.qty}</li>
                        <li className='list-group-item'>price:{product.price}</li>
                        
                        <li className='list-group-item'>GroupId:{group.name}</li>
                        
                    </ul>
                </div>
                </div>
                
            </div>
                   <div className='row'>
                    <div className="col">
                    <Link className="btn btn-primary " to="/pages/display">back</Link>
                    </div>
                    </div>
    </div>
    </div>
    
</div>

  )
}

export default Viewproductcard
