import React from 'react'
import Navbar from '../../../layouts/Navbar'
import LayoutHeading from '../../../layouts/layoutheading'
import { Link, useNavigate } from 'react-router-dom'
import { useState } from 'react'
import { IUserview } from '../models/IUserview'
import { Appdispatch, RootState, useAppDispatch } from '../../products/Redux/Store'
import * as useractions from "../../products/Redux/users/user.actions";
import * as userReducer from "../../products/Redux/users/user.reducer";
import { useSelector } from 'react-redux'
import Spinner from '../../../layouts/Spinner'
const UserRegister = () =>{
  const dispatch:Appdispatch = useAppDispatch();
  const navigate= useNavigate();

  //get user from redux
   const userState:userReducer.InitialState = useSelector((state:RootState)=>{
    return state[userReducer.userFeatureKey];
   })

   const {loading}= userState;
  const [user,setUser]  =useState<IUserview>({
         username:"",
         email:"",
         password:""

  })
  const updateInput = (event:React.ChangeEvent<HTMLInputElement>)=>{
       setUser({
            ...user,
            [event.target.name]:event.target.value
       })
  }
  const handlesubmit=(event:React.FormEvent <HTMLFormElement>)=>{
    event.preventDefault();
    dispatch(useractions.registerUserAction({user:user})).then((response:any)=>{
      if(response && !response.error){
        navigate('/users/login')
      }
    })
 //TODO  dispatch action
  }
  return (
    
      
       <div>
       {loading && <Spinner/>}
      <Navbar heading={"ProductManager"}/>
      <LayoutHeading heading={"RegisterUser"}/>
      <pre>{JSON.stringify(user)}</pre>
      <div className='container mt-3'>
        <div className='row'>
          <div className='col-sm-4'>
            <form onSubmit={e=>handlesubmit(e)}>
          <div className='mb-2'>
              <input type="text"
              required={true}
              name={'username'}
              value={user.username}
              onChange={e => updateInput(e)}
              className='form-control' placeholder={'username'}/>

            </div>
            <div className='mb-2'>
              <input type="email"
                required={true}
                name={'email'}
                value={user.email}
                onChange={e => updateInput(e)} className='form-control' placeholder={'email'}/>

            </div>
            <div className='mb-2'>
              <input type="password"
                required={true}
                name={'password'}
                value={user.password}
                onChange={e => updateInput(e)}className='form-control' placeholder={'password'}/>

            </div>
            <div className='mb-2'>
              <input type="submit" className='btn btn-success me-2' value={"Register"}/>
              <Link to={"/"} className="btn btn-danger">Cancel</Link>
            </div>
            </form>
            <small>Already have an account ?   <Link to={"/users/Login"} className='text-decoration-none text-success'>Login</Link></small>

          </div>
        </div>
      </div>
    </div>
    
  )
}

export default UserRegister
