import { createSlice, isRejectedWithValue } from "@reduxjs/toolkit";
import { IProductview } from "../../models/IProductview";
import { IGroupview } from "../../models/IGroupview";
import  * as productsActions from "../products/product.actions";
import { toastUtil } from "../../../util/toastUtil";

export const productFeatureKey = "productfeature";

export interface InitialState{
    products:IProductview[];
    product:IProductview;
    groups:IGroupview[];
    group:IGroupview;
    loading:boolean;
    errormessage:string|null;
}
const intialState:InitialState={
    products:[] as IProductview[],
    product:{} as IProductview,
    groups: [] as IGroupview[],
    group:{} as IGroupview,
    loading:false,
    errormessage:""
}
export const productSlice = createSlice({
    name:'productSlice',
    initialState:intialState,
    reducers:{},
    extraReducers:(builder)=>{
        //get all products

       builder.addCase(productsActions.getAllProductsAction.pending,(state)=>{
        state.loading=true;
       }).addCase(productsActions.getAllProductsAction.fulfilled,(state,action)=>{
        state.loading=false;
        state.products=action.payload;
       }).addCase(productsActions.getAllProductsAction.rejected,(state,action)=>{
        state.loading=false;
       if(isRejectedWithValue(action)){
        state.errormessage=action.payload.message;
       }
       })
       //get A product

       builder.addCase(productsActions.getAProductAction.pending,(state)=>{
        state.loading=true;
       }).addCase(productsActions.getAProductAction.fulfilled,(state,action)=>{
        state.loading=false;
        state.product=action.payload;
       }).addCase(productsActions.getAProductAction.rejected,(state,action)=>{
        state.loading=false;
       if(isRejectedWithValue(action)){
        state.errormessage=action.payload.message;
       }
       })
       //create A product

       builder.addCase(productsActions.createAProductsAction.pending,(state)=>{
        state.loading=true;
       }).addCase(productsActions.createAProductsAction.fulfilled,(state,action)=>{
        state.loading=false;
        state.products=action.payload;
        toastUtil.displaySuccessMessage("product is created");
       }).addCase(productsActions.createAProductsAction.rejected,(state,action)=>{
        state.loading=false;
        toastUtil.displayErrorMessage("Unable to create product");
       if(isRejectedWithValue(action)){
        state.errormessage=action.payload.message;
       }
       })
       //update A product

        builder.addCase(productsActions.updateProductsAction.pending,(state)=>{
        state.loading=true;
       }).addCase(productsActions.updateProductsAction.fulfilled,(state,action)=>{
        state.loading=false;
        state.product=action.payload;
        toastUtil.displaySuccessMessage("product is updated");
       }).addCase(productsActions.updateProductsAction.rejected,(state,action)=>{
        state.loading=false;
        toastUtil.displayErrorMessage("unable to update product");
       if(isRejectedWithValue(action)){
        state.errormessage=action.payload.message;
       }
       //delete A product

       })
       builder.addCase(productsActions.deleteProductsAction.pending,(state)=>{
        state.loading=true;
       }).addCase(productsActions.deleteProductsAction.fulfilled,(state,action)=>{
        state.loading=false;
        state.product={} as IProductview;
        toastUtil.displaySuccessMessage("product is deleted");
       }).addCase(productsActions.deleteProductsAction.rejected,(state,action)=>{
        state.loading=false;
        toastUtil.displayErrorMessage("unable to update a product");
       if(isRejectedWithValue(action)){
        state.errormessage=action.payload.message;
       }
       })
       //get All groups
       builder.addCase(productsActions.getAllGroupsAction.pending,(state)=>{
        state.loading=true;
       }).addCase(productsActions.getAllGroupsAction.fulfilled,(state,action)=>{
        state.loading=false;
        state.groups=action.payload;
       }).addCase(productsActions.getAllGroupsAction.rejected,(state,action)=>{
        state.loading=false;
       if(isRejectedWithValue(action)){
        state.errormessage=action.payload.message;
       }
       })
       //get A group

       builder.addCase(productsActions.getAGroupAction.pending,(state)=>{
        state.loading=true;
       }).addCase(productsActions.getAGroupAction.fulfilled,(state,action)=>{
        state.loading=false;
        state.group=action.payload;
       }).addCase(productsActions.getAGroupAction.rejected,(state,action)=>{
        state.loading=false;
       if(isRejectedWithValue(action)){
        state.errormessage=action.payload.message;
       }
       })
       
       
 }
})
   