import React, { useEffect } from 'react'
import { Link, useParams } from 'react-router-dom'
import Navbar from '../../../layouts/Navbar'
import LayoutHeading from '../../../layouts/layoutheading'
import { IProductview } from '../models/IProductview'
import { useState } from 'react'
import { ProductService } from '../services/ProductService'
import Spinner from '../../../layouts/Spinner'
import Errormessage from '../../../layouts/Errormessage'
import Viewproductcard from '../components/Viewproductcard'
import * as productReducer from '../Redux/products/product.reducer';
import * as productActions from '../Redux/products/product.actions';
import { useSelector } from 'react-redux'
import { RootState, useAppDispatch } from '../Redux/Store';


const Viewproduct = () => {
  const {productId}=useParams();
  const dispatch= useAppDispatch();

//get the state from redux
   const productReduxState:productReducer.InitialState= useSelector((state:RootState)=>{
    return state[productReducer.productFeatureKey]
   })
   const {product,errormessage,loading,group}=productReduxState;


  useEffect(()=>{
    if(productId){
      getdataFromserver(productId);
    }
  },[productId])//when the change happens

  useEffect(()=>{
    if(product && Object.keys(product).length>0){
      if(product.groupId){
     getGroupdataFromserver(product.groupId)
      }
    }
  },[])

 
  const getdataFromserver=(productId:string)=>{
    dispatch(productActions.getAProductAction({productId:productId}))

  }
  const getGroupdataFromserver=(groupId:string)=>{
  
    dispatch(productActions.getAGroupAction({groupId:groupId}))

  }
  
  return (
    <div>
       {loading && <Spinner/>}
        <Navbar heading={"Contact manager"}/>
        <LayoutHeading heading={"Viewproduct"}/>
        {!loading && errormessage && <Errormessage message={errormessage}/>} {/*if it is null know need to write the errorMessage > 0*/}
    
    {
      !loading && product && Object.keys(product).length>0 && group &&
      Object.keys(group).length >0 &&
      <Viewproductcard product={product}  group={group}/>
    }

    
    </div>
  )
}

export default Viewproduct
