import axios from "axios";
import { IProductview } from "../models/IProductview";
import { IGroupview } from "../models/IGroupview";
export class ProductService {
    private static ServerUrl:string = process.env.REACT_APP_EXPRESS_SERVER_URL? process.env.REACT_APP_EXPRESS_SERVER_URL :"";
    /**
   @usage: to Get all contacts
   @method: GET
   @params: no params
   @url: http://localhost:9999/contacts
   */
   
    public static getAllProducts():Promise<{data:IProductview[]}>{
        const dataUrl=`${this.ServerUrl}/products`;
        return axios.get(dataUrl);
    } ;
    /**
    @usage: to Get a contact
    @method: GET
    @params: no params
    @url: http://localhost:9000/:contactId
    */
    public static getAProduct(productId:string):Promise<{data:IProductview}>{
        const dataUrl=`${this.ServerUrl}/products/${productId}`;
        return axios.get(dataUrl); 
    }  
     /**
   @usage: to Create a contact
   @method: post
   @params: name,imageUrl,email,mobile,title,company,groupId
   @url: http://localhost:9000/contacts/
   */
   public static createProduct(product:IProductview):Promise<{data:IProductview}>{
    const dataUrl=`${this.ServerUrl}/products`;
    return axios.post(dataUrl,product);

}
/**
   @usage: to Update a contact
   @method: put
   @params: name,imageUrl,email,mobile,title,company,groupId
   @url: http://localhost:9000/:contactId
   */
   public static updateAProduct(productId:string,product:IProductview):Promise<{data:IProductview}>{
    const dataUrl=`${this.ServerUrl}/products/${productId}`;
    return axios.put(dataUrl,product);    
   }
   /**
   @usage: to Delete a contact
   @method: delete
   @params: name,imageUrl,email,mobile,title,company,groupId
   @url: http://localhost:9000/:contactId
   */
   public static  DeleteAProduct(productId:string):Promise<{data:IProductview}>{
    const dataUrl=`${this.ServerUrl}/products/${productId}`;
    return axios.delete(dataUrl);
}
   /**
   @usage: to Get all groups
   @method: GET
   @params: no params
   @url: http://localhost:9000/groups
   */

public static getAllgroups():Promise<{data:IGroupview[]}>{
    const dataUrl=`${this.ServerUrl}/groups`;
    return axios.get(dataUrl);
} 
 /**
   @usage: to Get a group
   @method: GET
   @params: no params
   @url: http://localhost:9000/groups/groupid
   */
   public static getAgroup(groupId:string):Promise<{data:IGroupview}>{
    const dataUrl=`${this.ServerUrl}/groups/${groupId}`;
    return axios.get(dataUrl);
} 
}