import React from 'react'
import { Link } from 'react-router-dom'
import { toast } from 'react-toastify'
import { toastUtil } from '../util/toastUtil'
import { TokenUtil } from '../util/TokenUtil'

const HomePage = () => {

  return (
    <div>
  <div className="landing-page">
        <div className="wrapper">
        <div className="d-flex justify-content-center align-items-end  text-end h-100 flex-column">
        <p className="display-2 text-black">BigBasket_App</p>
         {TokenUtil.IsLoggedIn()?
         <Link to="/pages/Display" className="btn btn-success">viewdetails</Link>:
         <Link to="/users/login" className="btn btn-success">Login</Link>}
         
    </div>
    </div>
    </div>
    
    </div>
  )
}

export default HomePage
