import React from 'react'
interface IProps{
    message:string;
}
const Errormessage:React.FC<IProps> = (props) => {
  return (
    <div>
      <div className='container mt-3'>
        <div className='row'>
            <h1 className='text-danger'>{props.message}</h1>
        </div>
      </div>
    </div>
  )
}

export default Errormessage
